<template>
  <div class="product-with-design-component">
    <Preloader size="80" v-if="isLoading"/>
    <div class="image-wrapper" v-else>
      <img
        :src="(currentColorIndex && product.product_image[currentColorIndex].image) || product.product_image[0].image"
        class="image-product"
      />
      <v-img
        class="image-logo"
        :src="currentDesign.image"
        :class="{'inverted': invertLogo}"
        :contain="true"
        :style="{ left: `${product.product_image[0].left}%`,
                      right: `${product.product_image[0].right}%`,
                      top: `${product.product_image[0].top}%`,
                      bottom: `${product.product_image[0].bottom}%`
        }"
        :position="product.product_image[0].horizontal_align + ' ' + product.product_image[0].vertical_align"
        @error="onDesignLoadingError"
      ></v-img>
    </div>
  </div>
</template>

<script>
import Preloader from "./Layout/Preloader";

export default {
  name: 'ProductWithDesign',
  components: {
    Preloader
  },
  props: ['product', 'design', 'name', 'currentColorIndex'],
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'namesList' });
  },
  updated() {
    console.log(this.currentColorIndex);  
  },
  computed: {
    isLoading() {
      return this.$store.state.namesList.isLoading || !this.design;
    },
    currentDesign() {
      let noImages = this.$store.state.designs.noImages[this.name];
      let namesList = this.$store.state.namesList.list;
      if (!namesList.find(item => item.slug === this.name)) {
        return Object.assign({}, this.design, { activeImage: this.design.image });
      }
      if (noImages && noImages.indexOf(this.design.slug) !== -1) {
        return Object.assign({}, this.design, { activeImage: this.design.image });
      }
      // replace logo default variant with demo, if it wasn't loaded
      return Object.assign({}, this.design, { activeImage: this.design.image.replace(/name(.[\w]+$)/, this.name + '$1') });
    },
    invertLogo() {
      let designLightness = this.design.lightness;
      let productLightness = this.product.product_image[0].lightness;
      return designLightness && productLightness && designLightness === productLightness;
    }
  },
  methods: {
    onDesignLoadingError() {
      this.$store.commit('setNoDesignImage', { nameSlug: this.name, designSlug: this.design.slug });
    }
  }
};
</script>

<style lang="scss">
  .product-with-design-component {
    .image-wrapper {
      position: relative;
      display: inline-block;
    }
    .image-logo {
      position: absolute;
    }
    .image-product {
      width: 100%;
    }
    .inverted {
      filter: invert(1);
    }
  }
</style>
